<template>
  <div class="mr-5">
    <p class="filter-label">{{ labelText }}</p>
    <v-menu
      v-model="treeView"
      transition="slide-y-transition"
      offset-y
      max-width="290px"
      min-width="420px"
      max-height="400px"
      :close-on-content-click="false"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          v-on="on"
          :value="inputValue"
          solo
          flat
          hide-details
          readonly
          @click:append="showTree"
        >
          <template v-slot:append>
            <v-icon
              class="drop-icon"
              :value="value"
              :class="{ 'drop-icon-revert': treeView }"
              @click="showTree"
              >mdi-menu-down</v-icon
            >
          </template>
        </v-text-field>
      </template>
      <v-treeview
        v-model="value"
        class="select-tree"
        open-all
        open-on-click
        selectable
        return-object
        :activatable="true"
        :items="items"
        selected-color="#00194D"
        @input="setValue(value)"
      ></v-treeview>
    </v-menu>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'SelectTreeFilter',
  created() {
    if (this.isRemote) {
      this.fetchItems();
    } else {
      this.items = this.localItems;
    }
    this.$store.state.filtersValues[this.filterName] = this.value;
  },
  methods: {
    fetchItems() {
      axios.get(this.filterUrl).then((response) => {
        this.items = this.changeKeyForItems(response.data.data);
      });
    },
    changeKeyForItems(items) {
      items.forEach((c) => {
        c.name = c.text;
        if (c.children) {
          this.changeKeyForItems(c.children);
        }
      });
      return items;
    },
    showTree() {
      this.treeView = !this.treeView;
    },
    setValue(value) {
      let valArr = value;
      if (valArr.length > 0) {
        let inputString = '';
        valArr.forEach((v, i) => {
          if (i === 0) {
            inputString += v.name;
          } else {
            inputString += ', ' + v.name;
          }
        });
        this.inputValue = inputString;
      } else this.inputValue = '';
    }
  },
  props: ['labelText', 'filterName', 'isRemote', 'filterUrl', 'localItems'],
  data: () => ({
    value: [],
    inputValue: '',
    treeView: false,
    items: []
  })
};
</script>

