<template>
  <v-expansion-panel v-model="panel">
    <v-expansion-panel-header hide-actions :ripple="false">
      <div>
        <p
          class="task-main-header"
          :class="{ 'complete-task': this.content.status === 'complete' }"
        >
          {{ content.header }}
        </p>
        <p
          class="task-time-header"
          :class="{ 'complete-task': this.content.status === 'complete' }"
        >
          {{ getTime }}
        </p>
      </div>
      <span class="icon icon-arrow-dropdown"></span>
    </v-expansion-panel-header>
    <v-expansion-panel-content>
      <div class="main-text" v-html="getTextWithoutBold"></div>
      <TaskButtons :complete="content.status === 'complete'" />
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>

<script>
import TaskButtons from './buttons/TaskButtons';
export default {
  name: 'Task',
  components: { TaskButtons },
  props: ['content'],
  data() {
    return {
      panel: 0
    };
  },
  computed: {
    getTime() {
      let date = new Date(this.content.recieve_time);
      let hours =
        date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
      let minutes =
        date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
      let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      let month =
        date.getMonth() + 1 < 10
          ? '0' + (date.getMonth() + 1)
          : date.getMonth() + 1;
      let year = date.getFullYear();

      return `${hours}:${minutes} ${day}.${month}.${year}`;
    },
    getTextWithoutBold() {
      return this.content.body
        .split('<b>')
        .join(' ')
        .split('</b>')
        .join(' ')
        .split('<br>')
        .join('<br><br>');
    }
  }
};
</script>

