<template>
  <v-card
    class="px-2 py-6 ma-5 tasks-main"
    v-resize="onResize"
    :style="getTasksHeight"
  >
    <div>
      <v-row class="page-title">
        <v-icon color="#ff7141" class="page-title-icon">icon-task</v-icon>
        <h3 class="page-title-title">Задачи: {{ getNewTasksSum }} новых</h3>
      </v-row>
    </div>
    <v-tabs v-model="tab" hide-slider>
      <v-tab
        :ripple="false"
        v-for="(item, i) in tabs"
        :key="i"
        @click="getTasksByStatus(i)"
      >
        {{ item }}</v-tab
      >
    </v-tabs>
    <v-expansion-panels multiple focusable flat>
      <Task v-for="(task, i) in tasks" :key="i" :content="tasks[i]" />
    </v-expansion-panels>
  </v-card>
</template>

<script>
import axios from 'axios';
import Task from '../components/Task';

export default {
  name: 'Tasks',
  components: { Task },
  mounted() {
    this.fetchTasks();
  },
  methods: {
    fetchTasks() {
      axios.get(this.getUrl).then((response) => {
        this.fullTasks = response.data.data;
        this.tasks = this.fullTasks.filter((t) => t.status === 'new');
        this.$store.commit('setTasks', this.tasks);
      });
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    getTasksByStatus(tab) {
      switch (tab) {
        case 0:
          this.tasks = this.fullTasks.filter((t) => t.status === 'new');
          break;
        case 1:
          this.tasks = this.fullTasks.filter((t) => t.status === 'complete');
          break;
        case 2:
          this.tasks = this.fullTasks;
          break;
      }
    }
  },
  computed: {
    getUrl() {
      return this.$store.state.isTestMode
        ? '../..' + this.defaultUrl
        : '/arm-isujt' + this.defaultUrl;
      // : this.serverUrl;
    },
    getTasksHeight() {
      let tableHeight = this.windowSize.y - 50;
      return `height: ${tableHeight}px`;
    },
    getNewTasksSum() {
      let sumArr = this.fullTasks.filter((t) => t.status === 'new');
      return sumArr.length;
    }
  },
  data() {
    return {
      tab: 0,
      tabs: ['Новые', 'Завершенные', 'Все'],
      defaultUrl: '/server/archive.json',
      serverUrl: '/ajax.php?module=Task&method=load',
      tasks: [],
      fullTasks: [],
      windowSize: {
        x: 0,
        y: 0
      }
    };
  }
};
</script>

