<template>
  <v-btn
    class="add-table mr-1"
    color="#4D6C99"
    height="25px"
    small
    dark
    depressed
    :ripple="false"
    @click="[addRecordToTable(), $emit('forceUpdateTable')]"
  >
    Добавить
  </v-btn>
</template>

<script>
export default {
  name: 'AddTableButton',
  props: ['record'],
  methods: {
    addRecordToTable() {
      if (this.$store.state.tableStores['rep_control_0']) {
        let uniqueRecord = this.$store.state.tableStores['rep_control_0'].every(
          (item) =>
            item.ser_loc !== this.record.ser_loc ||
            item.prs_loc !== this.record.prs_loc ||
            item.zns_loc !== this.record.zns_loc
        );
        if (uniqueRecord) {
          this.$store.commit('addTableStores', {
            key: 'rep_control_0',
            val: this.record
          });
        }
      } else {
        this.$store.commit('setTableStores', {
          key: 'rep_control_0',
          val: [this.record]
        });
      }
    }
  }
};
</script>

<style scoped lang="sass">
button.add-table
  width: 80px
  padding-top: 2px !important
  font: 12px/14px $body-font-family
  letter-spacing: 0.01px
  text-transform: none
</style>
